body {
    background-color: #e7ecf1;
}

h1,
h2,
h3,
h4,
p {
    margin-top: 0;
    margin-bottom: 0;
}

ul {
    margin: 0;
    padding-left: 0;

    list-style: none;
}

a {
    text-decoration: none;
    cursor: pointer;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

button {
    /* padding: 0; */
    /* border: none; */
    cursor: pointer;
}

.grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.gridItem {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 20px;
}
